import {Button, AppBar, Container, Toolbar, IconButton, Typography, Paper, Grid } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import {
    Link
  } from "react-router-dom";


  import useStyles from './UseStyles';
  import React from 'react';
  import ListItemIcon from '@material-ui/core/ListItemIcon';
  import ListItemText from '@material-ui/core/ListItemText';
  import HomeIcon from '@material-ui/icons/Home';
  import clsx from 'clsx';
  import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
  import List from '@material-ui/core/List';
  import Divider from '@material-ui/core/Divider';
  import ListItem from '@material-ui/core/ListItem';
  import InboxIcon from '@material-ui/icons/MoveToInbox';
  import ImageIcon from '@material-ui/icons/Image';
  import ContactMailIcon from '@material-ui/icons/ContactMail';

  import { makeStyles } from '@material-ui/core/styles';
  import { withStyles } from '@material-ui/core/styles';
  import Menu from '@material-ui/core/Menu';
  import MenuItem from '@material-ui/core/MenuItem';
  const StyledMenu = withStyles({
    paper: {
      border: '1px solid #d3d4d5',
    }
  
});






export default function Header() {





    const classes = useStyles();
    const [state, setState] = React.useState({
      top: false,
      left: false,
      bottom: false,
      right: false,
    });
  
    const toggleDrawer = (anchor, open) => (event) => {
      if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
        return;
      }
  
      setState({ ...state, [anchor]: open });
    };
  
    const list = (anchor) => (
      <div 
        className={clsx(classes.list, {
          [classes.fullList]: anchor === 'top' || anchor === 'bottom',
        })}
        role="presentation"
        onClick={toggleDrawer(anchor, false)}
        onKeyDown={toggleDrawer(anchor, false)}
      >




 
   
        
   <List>

   
          <Link style={{ textDecoration: 'none', color: '#3F51B5' }} to="/contact">

          <ListItem button>
            <ListItemIcon> <ContactMailIcon style={{ color: '#3F51B5' }}/> </ListItemIcon>
            <ListItemText  color='secondary'>Contacts</ListItemText>

          </ListItem>
          </Link>


          <Divider />
          <Link style={{ textDecoration: 'none', color: '#3F51B5', }} to="/write">
          <ListItem button>
            <ListItemIcon> <InboxIcon style={{ color: '#3F51B5' }}/> </ListItemIcon>
            <ListItemText primary="Write me" />
          </ListItem>
          </Link>
          <Divider />
          <Link style={{ textDecoration: 'none', color: '#3F51B5' }} to="/gallery">
          <ListItem button>
            <ListItemIcon> <ImageIcon style={{ color: '#3F51B5' }}/> </ListItemIcon>
            <ListItemText primary="Gallery" />
          </ListItem>
          </Link>
   
 


      <Divider />
      <Link style={{ textDecoration: 'none', color: '#3F51B5' }} to="/">
      <ListItem button>
            <ListItemIcon> <HomeIcon style={{ color: '#3F51B5' }}/> </ListItemIcon>
            <ListItemText primary="Home" />
          </ListItem>
          </Link>
               </List>
    </div>
  );


    return (

<div>
<AppBar poisiton="fixed" color="default">
<Container fixed>
<Toolbar>

<IconButton edge="start" color="inherit" aria-label="menu" className={classes.menuButton}>



{['left'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Button 
           aria-controls="customized-menu"
           aria-haspopup="true"
           variant="contained"
           color="default"
          onClick={toggleDrawer(anchor, true)}><MenuIcon/>
          </Button>
          <SwipeableDrawer
            anchor={anchor}
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
            onOpen={toggleDrawer(anchor, true)}
          >
            {list(anchor)}
          </SwipeableDrawer>
        </React.Fragment>
      ))}
   
   
   
  



</IconButton>

<Typography variant="h6" className={classes.title}> Pages</Typography>

    

</Toolbar>
</Container>
</AppBar>






<Paper className={classes.mainFeaturePost}
style={{backgroundImage:`url(https://source.unsplash.com/1600x900/?vintage)`}}>
<Container fixed>
<Grid item md={6}>
<div className={classes.mainFeaturePostContent}>
<Typography
component="h1"
variant="h3"
color="inherit"
gutterBottom
>
Hello world
</Typography>

<Typography
variant="h5"
color="inherit"
paragraph
> 
This is my homepage
</Typography>
<Link style={{ textDecoration: 'none' }} to="/">
<Button variant="contained" color="primary">
Learn more
</Button>
</Link>

    </div>
    </Grid>
    </Container>
    </Paper>




    </div>
)
}


