import React from 'react';
import { Button, TextField, Container, Typography } from '@material-ui/core';
import FormControl from '@material-ui/core/FormControl';
// import axios from 'axios';



  export default class Write extends React.Component {
    state = {
        name: '',
        contact: '',
        text: '',
      }
    
      handleChange = event => {
        this.setState({ name: event.target.value });
      }

      handleChange2 = event => {
        this.setState({ contact: event.target.value });
      }

      handleChange3 = event => {
        this.setState({ text: event.target.value });
      }
    
      handleSubmit = event => {
        event.preventDefault();
    


//     axios.post('telegram.php',{ name: this.state.name, 
//     contact: this.state.contact, 
//     text: this.state.text,})
//   .then(function(response){
//       console.log(response);
// }) 
      }


render() {
        return (
            <Container maxWidth="sm">
     
   
<Typography variant="h2" align="center" color="textPrimary" gutterBottom>
Write me
</Typography>
<Typography variant="h5" align="center" color="textSecondary" pargraph>
I will get back to you shortly
</Typography>

<br/><br/>




      
<form noValidate autoComplete="off" onSubmit={this.handleSubmit}>
    <FormControl fullWidth >
  <TextField id="standard-basic" label="Name" name="name"  placeholder="name" onChange={this.handleChange} /><br/>
  <TextField id="standard-basic" label="Contact" name="user_cntact"  placeholder="contact"  onChange={this.handleChange2} /><br/>
  <TextField id="standard-basic" label="Text" name="text" placeholder="text"  onChange={this.handleChange3} /><br/><br/>
<Button type="submit" variant="contained" disabled="true">Submit</Button>
  </FormControl>
</form>
<br/>

        </Container>
           
    
        
        )
}
  }